<template>
  <div class="article-view-container">
    <div class="title-tool-box">
      <div class="title">{{ article.title }}</div>
      <div class="tool">
        <v-btn
          color="primary"
          fab
          small
          dark
          @click="handleEdit"
        >
          <v-icon>
            mdi-pencil
          </v-icon>
        </v-btn>
      </div>

    </div>
    <div class="markdown-body text-left" v-html="contentHtml" />
  </div>
</template>

<script>
import { view } from '@/api/modules/article'
import { mavonEditor } from 'mavon-editor'
import 'github-markdown-css'

const markdownIt = mavonEditor.getMarkdownIt()
export default {
  name: 'ArticleView',
  data() {
    return {
      article: {
        id: null,
        title: null,
        content: null,
        createUserName: null
      }
    }
  },
  computed: {
    contentHtml() {
      if (this.article.content) {
        return markdownIt.render(this.article.content)
      } else {
        return null
      }
    }
  },
  created() {
    this.initData(this.$route.params.id)
  },
  methods: {
    initData(id) {
      view({ id }).then(data => {
        this.article = data.data
      })
    },
    handleEdit() {
      this.$router.push(`/article/${this.article.id}/edit`)
    }
  }
}
</script>

<style lang="scss" scoped>
.article-view-container {
  .title-tool-box {
    display: flex;

    .title {
      text-align: center;
      font-size: 15px;
      flex: 1;
    }

    .tool {
      width: 80px;
    }
  }
}
</style>
