import request from '@/utils/request'

export function list({ pageNo, pageSize, keyword }) {
  return request({
    url: '/article/list',
    method: 'get',
    params: {
      pageNo,
      pageSize,
      keyword
    }
  })
}

export function view({ id }) {
  return request({
    url: '/article/' + id,
    method: 'get'
  })
}

export function edit({ id, data }) {
  return request({
    url: '/article/' + id,
    method: 'post',
    data: data
  })
}

export function upload({ file }) {
  const formData = new FormData()
  formData.append('file', file)
  return request({
    url: '/file/upload/image',
    method: 'post',
    data: formData
  })
}

export function add({ data }) {
  return request({
    url: '/article',
    method: 'post',
    data: data
  })
}
